.illustration_inner_wrapper {
  position: absolute;
}

.illustration_wrapper {
  position: relative;
}

.body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sunburst_wrapper {
  margin: 1rem;
  height: 10rem;
  width: 10rem;
}

.sunburst_points {
  font-family: "Righteous";
  font-size: 1.875rem;
  color: var(--coral);
}

.title {
  font-family: "Rubik";
  font-weight: 500;
}

.text {
  padding-top: 0.5rem;
  padding-bottom: 1rem;
  max-width: 300px;
}
